        width: 30%;
    }
}

.recipe_image__34WkO {
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

.recipe_ingredientsList__1h7rP {
    list-style-type: none;
    width: 80%;
}

.recipe_caloriesValue__33MtT {
    color: #ff867a;
}
.App {
  min-height: 100vh;
  background-image: -webkit-gradient(linear, left top, right top, from(#ff8177), color-stop(0%, #ff867a), color-stop(21%, #ff8c7f), color-stop(52%, #f99185), color-stop(78%, #cf556c), to(#b12a5b));
  background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: #b12b5b;
  border: none;
  padding: 10px 20px;
  color: #ffffff;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}


